      sud:"ਕੁੱਲ 1,255 ਲੱਖ ਟਨ ਗੰਨਾ ਉਤਪਾਦਨ ਦੇ ਨਾਲ 21.5 ਲੱਖ ਹੈਕਟੇਅਰ ਵਿੱਚ ਗੰਨਾ ਉਗਾਇਆ ਜਾਂਦਾ ਹੈ। ਗੰਨੇ ਦੇ ਉਤਪਾਦਨ ਦਾ ਲਗਭਗ 40% ਗੁੜ ਅਤੇ ਖੰਡਸਰੀ ਉਦਯੋਗ ਵੱਲ ਮੋੜਿਆ ਜਾਂਦਾ ਹੈ ਅਤੇ 48% ਖੰਡ ਉਤਪਾਦਨ ਲਈ ਪਿੜਾਈ ਜਾਂਦੀ ਹੈ, ਜੋ ਕਿ ਲਗਭਗ 57.8 ਲੱਖ ਟਨ ਹੈ। ਗੰਨੇ ਦੀ ਉਤਪਾਦਕਤਾ ਲਗਭਗ 65 ਟਨ/ਹੈਕਟੇਅਰ ਹੈ। ਉਤਪਾਦਨ ਵਿੱਚ ਵੱਡੀਆਂ ਰੁਕਾਵਟਾਂ ਹਨ ਸੁਧਰੀਆਂ ਕਿਸਮਾਂ ਦੇ ਗੁਣਵੱਤਾ ਵਾਲੇ ਬੀਜ ਦੀ ਨਾਕਾਫ਼ੀ ਉਪਲਬਧਤਾ, ਲਾਲ ਸੜਨ ਅਤੇ ਮੁਰਝਾਏ ਰੋਗ, ਜ਼ਮੀਨ ਦੀ ਮਾੜੀ ਉਪਜਾਊ ਸ਼ਕਤੀ ਅਤੇ ਅਸੰਤੁਲਿਤ ਖਾਦ ਦੀ ਵਰਤੋਂ ਕਾਰਨ ਘੱਟ ਫਸਲ ਉਤਪਾਦਕਤਾ, ਰੇਤੂਨ ਫਸਲ ਪ੍ਰਬੰਧਨ ਵੱਲ ਘੱਟ ਧਿਆਨ। ਮਹੱਤਵਪੂਰਨ ਦਖਲਅੰਦਾਜ਼ੀ ਹਨ ਗੰਨੇ ਦੀਆਂ ਕਿਸਮਾਂ CoSe 96234, CoSe 96436 ਹੜ੍ਹ ਪ੍ਰਭਾਵਿਤ ਪੂਰਬੀ ਉੱਤਰ ਪ੍ਰਦੇਸ਼ ਵਿੱਚ, 3-ਪੱਧਰੀ ਬੀਜ ਪ੍ਰੋਗਰਾਮ ਦੁਆਰਾ ਸੁਧਰੀਆਂ ਕਿਸਮਾਂ ਦੇ ਗੁਣਵੱਤਾ ਵਾਲੇ ਬੀਜ ਦਾ ਵੱਡੇ ਪੱਧਰ 'ਤੇ ਉਤਪਾਦਨ, ਉਤਪਾਦਕਤਾ ਨੂੰ ਵਧਾਉਣ ਲਈ ਰੈਟੂਨ ਪ੍ਰਬੰਧਨ ਅਭਿਆਸਾਂ ਨੂੰ ਅਪਣਾਉਣਾ, ਉਚਿਤ ਸੰਰਚਨਾ ਦੇ ਨਾਲ-ਨਾਲ ਢੁਕਵੀਂ ਵਿਵਸਥਾ। ਪਤਝੜ ਵਿੱਚ ਬੀਜੇ ਗਏ ਗੰਨੇ ਵਿੱਚ ਏਕੀਕ੍ਰਿਤ ਕੀਟ ਪ੍ਰਬੰਧਨ ਦੇ ਅਭਿਆਸ ਵਜੋਂ ਅਤੇ ਆਲੂ, ਸਰ੍ਹੋਂ, ਦਾਲ, ਰਾਜਮਾਸ਼, ਮੂੰਗਬੀਨ, ਛੋਲੇ, ਧਨੀਆ, ਮੂਲੀ, ਬਰਸੀਮ, ਸੇਂਜੀ ਅਤੇ ਜਵੀ ਨੂੰ ਅੰਤਰ-ਫਸਲ ਵਜੋਂ ਉਗਾਓ।",
