      data1: 'एक चावल ट्रांसप्लांटर एक विशेष ट्रांसप्लांटर होता है जिसे धान के खेत में चावल के पौधों को ट्रांसप्लांट करने के लिए लगाया जाता है। मुख्य रूप से दो तरह के राइस ट्रांसप्लांटर यानी राइडिंग टाइप और वॉकिंग टाइप। राइडिंग प्रकार बिजली से संचालित होता है और आमतौर पर एक पास में छह लाइनों को ट्रांसप्लांट कर सकता है। दूसरी ओर, चलने का प्रकार मैन्युअल रूप से संचालित होता है और आमतौर पर एक पास में चार लाइनों को ट्रांसप्लांट कर सकता है।',
      data2: 'ट्रैक्टर आमतौर पर खेती से जुड़े होते हैं क्योंकि किसान उनका उपयोग मशीनरी के साथ जुताई, जुताई, बुवाई और हैरोइंग जैसे उपकरणों के लिए करते हैं। इसके अलावा, ट्रैक्टर का उपयोग मशीनरी को धक्का देने या खींचने के लिए किया जाता है, जिससे खेती के संचालन को और अधिक सुविधाजनक बना दिया जाता है।',
      data3: 'बेलर इंप्लीमेंट का उपयोग फसलों जैसे घास, कपास, पुआल आदि को संपीड़ित करने के लिए किया जाता है, जब इसे संकुचित किया जाता है तो इसे संभालना, परिवहन और भंडारण करना आसान होता है। घास को संपीड़ित करने के बाद बेलनाकार बेल को सुतली या धागे से बांधा जा सकता है। फिर इन गांठों को मवेशियों को खिलाने के लिए बड़ी डेयरियों में स्थानांतरित किया जा सकता है।',
